.cart {
  background-color: #fff;
  box-shadow: 0px 1px 6px 0px rgba(0, 0, 0, 0.26);
  &__total {
    text-align: center;
    background-color: #f5f5f5;
    padding-top: 35px;
    padding-bottom: 35px;
    @include flex(center, center);
    @include md {
      padding: 20px; } }
  &__total-title {
    margin-top: .5em;
    margin-right: 10px; }
  &__btns {
    padding: 30px;
    @include flex(space-between, center);
    @include md {
      padding: 15px; }
    @include xs {
      display: block;
      text-align: center; } }
  &__btn {
    @include xs {
      .btn {
        width: 100%; }
      @include notlast {
        margin-bottom: 10px; } } } }
